.fullspace-master-div {
  background-color: #dfdee3;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.master-div {
  background-image: linear-gradient(
    #f3f5f4,
    #f3f5f4 30%,
    #d2d2d4 75%,
    #d2d2d4 92%,
    #373028 98%,
    #373028 100%
  );
  color: white;
  border-right: solid 1px;
  border-left: solid 1px;
  width: 95%;
  height: 93%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 70% 30%;
  grid-template-columns: 70% 30%;
  -ms-grid-rows: 7% 68% 19% 6%;
  grid-template-rows: 7% 68% 19% 6%;
  grid-template-areas:
    "hd hd"
    "main main"
    "plaque ."
    "ft ft";
}

.top-white-space-div {
  grid-row: 1;
  grid-column: 1;
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-area: hd;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    #373028 0%,
    #d2d2d4 0.5%,
    #d2d2d4 70%,
    #ebecf1 75%,
    #ebecf1 100%
  );
}

.art {
  color: none;
  grid-row: 2;
  grid-column: 1;
  -ms-grid-row: 2;
  -ms-grid-column: 1;
  -ms-grid-column-span: 2;
  grid-area: main;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: unquote("(minmax(10%, 25%))[4]");
  grid-template-columns: repeat(4, minmax(10%, 25%));
  -ms-grid-rows: unquote("(45%)[2]");
  grid-template-rows: repeat(2, 45%);
  grid-gap: 5%;
  width: 90.5%;
  height: 100%;
  object-fit: contain;
  -ms-grid-column-align: center;
  justify-self: center;
  -ms-grid-row-align: center;
  align-self: center;
}

img {
  width: 100%;
  max-height: 95%;
  object-fit: contain;
  -ms-grid-row-align: center;
  align-self: center;
  -ms-grid-column-align: center;
  justify-self: center;
  cursor: pointer;
}

.quad-image {
  width: 85%;
  max-height: 50%;
  object-fit: contain;
  -ms-grid-row-align: center;
  align-self: center;
}

@-moz-document url-prefix() {
  .quad-image {
    max-height: 100%;
  }
}

.b {
  -ms-grid-column: 2;
  -ms-grid-column-span: 1;
  grid-column: 2/3;
  -ms-grid-row: 1;
  -ms-grid-row-span: 2;
  grid-row: 1/3;
  max-height: 100%;
