.master-return-letter {
  padding: 0px;
  margin: 0px;
  position: relative;
  width: 80%;
  height: 95px;
}

.letter-top-triangle {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url(/Artistry/static/media/envelope-lid.705af67c.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 3;
  transition: 1.7s 2s ease-in;
  transform-origin: top;
  transform: rotateX(0deg);
}

.letter-top-triangle-open {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url(/Artistry/static/media/envelope-lid.705af67c.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 0;
  transition: 1.7s ease-in;
  transform-origin: top;
  transform: rotateX(180deg);
}

.envelope {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/Artistry/static/media/recovers.24af8733.png);
  background-size: cover;
  z-index: 3;
}

.inside-letter {
  position: absolute;
  width: 100%;
  height: 100%;
  max-height: 100%;
  top: 0px;
  background-image: url(/Artistry/static/media/letter-full-size2.2b107f9d.jpg);
  background-size: cover;
  z-index: 1;
  transition: 2.5s 0s ease-in;
  overflow: hidden;
}

.inside-letter-open {
  width: 100%;
  height: 110%;
  max-height: 110%;
  top: -100%;
  position: absolute;
  background-image: url(/Artistry/static/media/letter-full-size2.2b107f9d.jpg);
  background-size: cover;
  z-index: 2;
  transition: 3s 0.5s ease-in;
  overflow: hidden;
}

.challenge-p {
  color: #271c04;
  font-family: "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 13px;
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 0px;
}

.letter-back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  background-image: url(/Artistry/static/media/letter-full-size.0ce20502.jpg);
  background-size: cover;
  z-index: 0;
  box-shadow: 0px 9px 10px -9px #6e6c69;
}

.sealopen {
  cursor: pointer;
  position: absolute;
  justify-content: center;
  align-items: center;
  background-image: url(/Artistry/static/media/seal-cracked.a54672e3.png);
  background-repeat: no-repeat;
  background-size: contain;
  width: 15%;
  height: 40%;
  left: 42%;
  top: 60%;
  transition: 2s;
  z-index: 5;
}

.seal {
  cursor: pointer;
